.pagination-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination-button:not(:last-child), .dots {
  margin-right: 16px;
}

.pagination-button {
  background-color: $ white-text-color;
  color: $ primary-text-color;
  cursor: pointer;
  border: none;
  border-radius: 50px;
  width: 40px;
  height: 40px;
  padding: 0;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.333;
}

.pagination-button:hover {
  transform: scale(1.03);
  box-shadow: 0 .9px 10px 2px #0d96fadd;
}

.pagination--current {
  color: #fffS;
  background-color: #0d96fadd;
  border-radius: 50px;
  width: 40px;
  height: 40px;
}

.arrow-left, .arrow-right {
  background-color: $ secondary-background-color;
  stroke: #000;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 5px;
  width: 40px;
  height: 40px;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.arrow-left {
  background-image: url("filmoteka-icons.12e6e143.svg#icon-arrow-left");
}

.arrow-right {
  background-image: url("filmoteka-icons.12e6e143.svg#icon-arrow-right");
}

@media screen and (min-width: 768px) {
  .pagination-container {
    padding: 50px 0 60px;
  }
}

@media screen and (max-width: 768px) {
  .dots, .first-button, .last-button {
    display: none;
  }
}

